.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Div_Input {
  max-width: 800px;
  width: 80%;
  display: flex;
  justify-content: space-between;
  padding: 10px 0px 20px 0px;
  flex-wrap: wrap;
}

.Span {
  font-size: 24px;
  min-width: 200px;
}

.Input {
  max-width: 400px;
  width: 70%;
  padding: 5px;
  border-radius: 5px;
}

.Button {
  position: relative;
  background-color: #04aa6d;
  border: none;
  font-size: 28px;
  color: #ffffff;
  padding: 20px;
  width: 200px;
  text-align: center;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
  text-decoration: none;
  overflow: hidden;
  cursor: pointer;
  border-radius: 5px;
}

.Button:after {
  content: "";
  background: #90ee90;
  display: block;
  position: absolute;
  padding-top: 300%;
  padding-left: 350%;
  margin-left: -20px !important;
  margin-top: -120%;
  opacity: 0;
  transition: all 0.8s;
}

.Button:active:after {
  padding: 0;
  margin: 0;
  opacity: 1;
  transition: 0s;
}
